.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  /* background-color: red; */
  margin: 20px;
}

.subcontainer {
  display: flex;
  flex-direction: column;
  /* background-color: rgb(100, 140, 163);  */
  background-color: #b8d8be; 
  padding: 20px;
  margin: 15px;
  border-radius: 10px;
  border-width: 2px;
  box-shadow: 2px 2px gray;
  width: 30%;
  min-width: 250px;
  align-items: center;
}

.title {
  font-weight: bold;
  font-size: medium;
  margin-bottom: 15px;
}

