#children {
  margin: 10px;
  width: 80%;
  min-width: 280px;
  /* background-color: #61dafb; */
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

#title {
  margin-right: 5px;
}

.resultContainer {
  display: flex;
  justify-content: center;
  width: 50%;
  /* background-color: #61dafb; */
}



.inputBox {
  width: 50%;
  min-width: 120px;
  margin: 0px 5px;
  padding: 5px 5px;
  border-radius: 5px;
  background-color: #e8f4ea;
  font-size: medium;
  border-width: 1px;
  border-color: rgb(224, 219, 219);
}


#result {
  background-color: yellowgreen;
  padding: 10px;
  min-width: 90px;
  text-align: center;
  border-radius: 3px;
  border-width: 1px;
  border-style: solid;
}

.dropdown {
  width: 180px;
  /* height: 50px; */
  align-self: flex-end;
}

.doseBar {
  display: flex;
  /* background-color: #61dafb; */
  justify-content: space-evenly;
  align-items: center;
  width: 60%;
  min-width: 250px;
}

.doseBar .inputBox {
  width: 50%;
  /* background-color: red; */
  min-width: 50px;
  margin-right: 5px;
}

.doseBar .sub {
  width: 35%;
  /* background-color: red; */
  text-align: center;
}







.MuiTextField-root {
  background: #e8f4ea;
}

